@import '../../base';

.pet-illness-desc {
  .input-wrapper-desc {
    font-size: $font-size-regular * 0.95;
    font-weight: $font-weight-normal;
    color: $color-body;

    @media screen and (min-width: 577px) {
      transform: translateY(30px);
    }
  }
}

.illness-input {
  .input-wrapper-desc {
    font-size: $font-size-small;
    font-weight: $font-weight-normal;
    color: $color-caption;
  }
}
